.back {
    background: #fff6ea;
    height: 100vh;
    transition: all 0.5s;
    color: "white";
}

.close {
    background-color: #fff6ea;
    /* Green */
    border: none;
    color: white;
    padding: 10px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 10px;
}

.close:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.utilbtn {
    margin-top: 10px;
}